import styled from "styled-components";
import {colors} from '../../colors';
import iconMenu from "../../assets/icon-menu.svg";
import iconClose from "../../assets/icon-close.svg";
import iconSearch from "../../assets/icon-search.svg";

interface SidebarProps {
    icon: boolean
}


export const Header = styled.header`
  width: 100%;
  height: 60px;
  background-color: ${colors.primary};

  display: flex;
  align-items: center;
  padding: 0 25px;
  position: relative;

`;

export const ButtonSearch = styled.button`
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: transparent;

  background-image: url(${iconSearch});

  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  right: 65px;
`;

export const ButtonMenu = styled.button<SidebarProps>`
  width: 20px;
  height: 20px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: transparent;

  background-image: url(${props => !props.icon ? iconMenu : iconClose});
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  right: 25px;
  z-index: 10;
`;

export const MenuMobileContainer = styled.div`
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 9;
  display: grid;
  grid-template-columns: 1fr 65px;
  background: rgba(0, 0, 0, 0.5);
  transition: 0.333s 0.333s ease-in-out;
  visibility: hidden;
  opacity: 0;

  &.opened {
    left: 0;
    transition: 0.333s ease;
    visibility: visible;
    opacity: 1;

    nav {
      transition: 0.333s 0.333s ease-in-out;
      transform: translateX(0);
    }
  }

  @media (min-width: 640px) {
    width: 300px;
    overflow: hidden;
    left: auto;
    right: 0;
    grid-template-columns: 1fr 0;


    &.opened {
      left: auto;
      right: 0;
      height: 100vh;
      z-index: 10;
      box-shadow: 0 0 0 1px ${colors.primary};
    }
  }

`;

export const MenuMobile = styled.nav`
  background: #FFF;
  display: flex;
  flex-direction: column;
  grid-row-gap: 15px;
  padding: 0 25px;
  transition: 0.333s ease-in-out;
  transform: translateX(-100%);

  .button-close {
    display: none;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    right: 25px;
    background: url(${iconClose}) ${colors.primary} center no-repeat;
    background-size: 14px;
    cursor: pointer;
  }

  .head {
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    margin-bottom: 40px;
  }

  h4 {
    text-transform: uppercase;
    font-weight: 800;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.25);
    letter-spacing: 0.1em;
  }

  hr {
    height: 1px;
    width: 100%;
    background: #E1E1E1;
    border: none;
  }

  ul {
    display: flex;
    flex-direction: column;
    grid-row-gap: 15px;

    li {
      a {
        text-transform: uppercase;
        text-decoration: none;
        letter-spacing: 0.05em;
        color: #000;
        font-size: 14px;
        font-weight: 800;
      }
    }

  }

  @media (min-width: 640px) {
    padding-top: 25px;
    transform: translateX(100%);
    

    .button-close {
      display: block;
    }

    .head {
      margin-top: 15px;
      display: none;
    }


  }




`;





