import styled from "styled-components";
import {colors} from "../../colors";

export const FooterContainer = styled.footer`
  width: 100%;
  height: 80px;
  background-color: ${colors.primary};
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  letter-spacing: 0.05em;
  
  span{ 
    padding: 0 20px;
    text-align: center;
    
    b {
      white-space: nowrap;
    }
  }
`;
