import styled from "styled-components";
import {colors} from "../../colors";
import iconClose from "../../assets/icon-close.svg";

interface ModalProps {
    show?: boolean,
    gridNumber?: number,
    load?: boolean
}

export const ModalContainer = styled.div<ModalProps>`
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0);
  display: grid;
  grid-template-rows: 70px 1fr;
  overflow-x: auto;
  transform: translateY(100%);
  transition: transform 1s 0.3s ease;

  &.show {
    transform: translateY(0);
    transition: 1s ease;
    background-color: rgba(0, 0, 0, 0.5);

    div:last-child {
      transform: translateY(0);
      transition: 1s 0.3s ease;
    }

  }

`;

export const ModalClose = styled.div`
  width: 100%;
  height: 70px;
`;

export const ModalBody = styled.div`
  margin: 0 auto;
  width: 100%;
  max-width: 1150px;
  background-color: #FFFFFF;
  border-radius: 20px 20px 0 0;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateY(100%);
  transition: 1s ease;
  position: relative;

  .close-button {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    position: absolute;
    top: 15px;
    right: 15px;
    background: url(${iconClose}) #CCCCCC center center no-repeat;
    background-size: 12px 12px;
    transition: 0.333s ease;

    &:hover {
      background-color: ${colors.primary};
      cursor: pointer;
    }
  }

`;

export const ModalContent = styled.div<ModalProps>`
  width: 85%;
  max-width: 850px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 60px;
  position: relative;
  transition: 0.5s ease;
  opacity: ${props => props.load ? 1 : 0.2};



  small {
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-family: "Roboto Bk", sans-serif;
    font-weight: 800;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.3);
    margin-bottom: 5px;
  }

  h4 {
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-family: "Roboto Bk", sans-serif;
    font-size: 18px;
    font-weight: 900;
    color: ${colors.primary};
  }

  .genres {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    margin-top: 10px;

    span {
      height: 20px;
      padding: 0 15px;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      text-transform: uppercase;
      letter-spacing: 0.05em;
      align-items: center;
      font-size: 10px;
      font-weight: 900;
      background-color: rgba(0, 0, 0, 0.1);
      transition: 0.333s ease;
      white-space: nowrap;

      &:hover {
        background-color: ${colors.primary};
        color: #FFF;
      }
    }
  }

  .movie-details {
    display: flex;
    flex-direction: column;

    .poster {
      display: flex;
      justify-content: center;
      padding: 50px 0;

      img {
        width: 192px;
        height: 294px;
        border-radius: 10px;
        object-position: center;
        object-fit: cover;
      }
    }

    .movie-info {
      display: flex;
      flex-direction: column;

      ul.info {
        display: flex;
        flex-direction: column;
        grid-row-gap: 15px;

        li {
          font-size: 13px;

          b {
            text-transform: uppercase;
          }

          p {
            margin-top: 10px;
            text-align: justify;
            font-size: 15px;
          }
        }
      }

      h5 {
        text-transform: uppercase;
        font-size: 13px;
        margin: 30px 0 25px 0;
      }

      ul.cast {
        display: flex;
        flex-direction: column;
        grid-row-gap: 25px;
        grid-column-gap: 10px;


        li {
          display: grid;
          align-items: center;
          grid-template-columns: 60px 1fr;
          margin-bottom: 5px;

          img {
            width: 45px;
            height: 45px;
            border-radius: 50%;
            object-fit: cover;
            object-position: center;

            box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
          }

          div {
            display: flex;
            flex-direction: column;

            b, span {
              font-size: 13px;
            }
          }

        }
      }
    }
  }

  @media (min-width: 640px) {
    ul.cast {
      display: grid !important;
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (min-width: 768px) {
    .movie-details {
      display: grid;
      grid-template-columns: 240px 1fr;
      grid-column-gap: 25px;
      margin-top: 40px;

      .poster {
        padding: 0;

        img {
          width: 235px;
          height: 360px;
        }
      }

    }
  }

  @media (min-width: 1024px) {
    ul.cast {
      display: grid !important;
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

`;

export const SimilarMovies = styled.div`
  width: 85%;
  max-width: 850px;
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;
  transition: 0.5s ease;

 
  
  h3 {
    width: 100%;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
`;

export const SimilarMoviesCards = styled.div<ModalProps>`
  
  display: grid;
  grid-template-columns: repeat(${props => props.gridNumber}, 1fr);
  grid-gap: 20px;
  
  div:first-child{
    margin: 0 auto;
  }
  
  div {
    min-width: 120px;
    max-width: 120px;
    height: 180px;
    margin: 0 auto;


    img {
      height: 180px;
    }
  }
`;

