import styled from "styled-components";

interface MovieCardStyleProps {
    show: boolean
}

export const Card = styled.div<MovieCardStyleProps>`
  order: ${props => props.show ? 1 : 2};
  width: 100%;
  min-width: 150px;
  max-width: 150px;
  height: 230px;
  margin-top: 20px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  background: #FFF;
  display:${props => props.show ? 'flex' : 'none'} ;
  justify-content: center;
  transition: 0.333s ease;
  cursor: pointer;
  opacity: ${props => props.show ? 1 : 0.5};


  &.hidden {
    transform: scale(0);
  }

  img {
    width: 100%;
    height: 230px;
    object-fit: cover;
    object-position: center;
  }

  .title {
    height: 80px;
    width: 110px;
    position: absolute;
    bottom: 0;
    text-align: center;
    opacity: 1;
    display: flex;
    align-items: center;
    padding-bottom: 5px;
    justify-content: center;

    span {
      text-transform: uppercase;
      font-weight: 900;
      font-size: 13px;
      opacity: 0;
      z-index: 2;
    }

    &::before {
      content: '';
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background: #FFF;
      position: absolute;
      top: 140%;
      transition: 0.333s ease;
    }
  }

  &:first-child {
    margin-left: 20px;
  }

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 5px -16px rgba(0, 0, 0, 0.25), 0 0 0 1px rgba(0, 0, 0, 0.1);

    .title::before {
      box-shadow: 0 0 0 120px #FFF;
    }

    span {
      opacity: 1;
      transition: 0.333s 0.166s ease;
    }
  }
`;
