import React from 'react';
import {BrowserRouter} from "react-router-dom";
import Home from './pages/home';
import Sidebar from "./components/sidebar";
import Footer from "./components/footer";

const Routes = () => {
    return (
        <BrowserRouter>
            <Sidebar/>
            <Home/>
            <Footer/>
        </BrowserRouter>
    );
}

export default Routes;
