import React, {useEffect, useState} from "react";
import {ModalBody, ModalClose, ModalContainer, ModalContent, SimilarMovies, SimilarMoviesCards} from './styles';

import {MovieData} from "../../interfaces";
import MovieCard from "../movie-card";
import "react-multi-carousel/lib/styles.css";

interface ModalProps {
    show: boolean,
    data?: MovieData,
    similar: MovieData[] | undefined,

    getSimilarMovieID(id: number, show: boolean): void

    closeModal(closeModal: boolean): void
}

const Modal = (props: ModalProps) => {

    const [modalShow, setModalShow] = useState<boolean>();
    const [gridNumber, setGridNumber] = useState<number>()
    const [load, setLoad] = useState<boolean>(true)

    useEffect(() => {
        setModalShow(props.show)
        getWidthWindow()
    }, [props.show])

    const handleClickModal = (evt: any) => {

        const safeArea = evt.classList.contains(ModalContainer.styledComponentId) ||
            evt.classList.contains(ModalClose.styledComponentId);

        if (safeArea) {
            props.closeModal(false)
            setModalShow(false);
        }
    }

    const handleCloseModal = () => {
        props.closeModal(false)
        setModalShow(false);
    }

    const handleSimilarMovieCardClick = (id: number, show: boolean) => {
        setLoad(false)
        setTimeout(async () => {
            props.getSimilarMovieID(id, show)
            setLoad(true)
        }, 500);

        document.getElementsByClassName(ModalContainer.styledComponentId)[0].scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth"
        })

    }

    window.addEventListener('resize', () => getWidthWindow());

    const getWidthWindow = () => {

        const moviesCardContainerWidth = 940;
        const windowWidth = window.innerWidth;
        const cardsNumber = windowWidth > moviesCardContainerWidth ? moviesCardContainerWidth / 130 : (windowWidth - ((windowWidth / 100) * 15)) / 130;
        const cardsByGrid = Math.floor(cardsNumber) >= 7 ? 6 : Math.floor(cardsNumber);
        setGridNumber(cardsByGrid)

    }


    return (
        <ModalContainer onClick={(e) => handleClickModal(e.target)}
                        className={modalShow ? 'show' : ''}>

            <ModalClose/>

            <ModalBody>
                <div className="close-button"
                     onClick={handleCloseModal}/>
                {props.data && (<ModalContent load={load}>
                    <small>{props.data.type}</small>
                    <h4>{props.data.title}</h4>
                    <div className="genres">
                        {
                            props.data.genres.map(genre => (
                                <span key={genre.id}>{genre.type}</span>
                            ))
                        }

                    </div>

                    <div className="movie-details">
                        <div className="poster">
                            <img src={props.data.poster} alt={props.data.title}/>
                        </div>
                        <div className="movie-info">
                            <ul className="info">
                                <li><b>lançamento:</b> <span>{props.data.releaseDate}</span></li>
                                <li><b>duração:</b> <span>{props.data.duration} min</span></li>
                                <li><strong>IMDb:</strong> <span>{props.data.rating}/10</span></li>
                                <li>
                                    <b>sinopse</b>
                                    <p>{props.data.synopsis}</p>
                                </li>
                            </ul>

                            <h5>Elenco Principal</h5>

                            <ul className="cast">

                                {
                                    (props.data.cast.map((actor, index) => (
                                        <li key={index}>
                                            <img
                                                src={actor.photo}
                                                alt={actor.actor}/>
                                            <div>
                                                <b>{actor.actor}</b>
                                                <span>{actor.character}</span>
                                            </div>
                                        </li>
                                    )))
                                }


                            </ul>

                        </div>
                    </div>


                </ModalContent>)}

                {props.similar?.length !== 0 && (
                    <SimilarMovies>
                        <h3>Filmes semelhantes</h3>


                        <SimilarMoviesCards gridNumber={gridNumber}>
                            {
                                props.similar?.slice(0, 6)
                                    .map(movie => (
                                        <MovieCard key={movie.id}
                                                   id={movie.id}
                                                   poster={movie.poster}
                                                   title={movie.title}
                                                   show={true}
                                                   handleClick={handleSimilarMovieCardClick}/>

                                    ))
                            }
                        </SimilarMoviesCards>
                    </SimilarMovies>
                )}
            </ModalBody>

        </ModalContainer>
    );
}

export default Modal;
