import React, {useEffect, useState} from "react";
import Modal from "../../components/modal";
import Releases from "../../components/releases";

import {movies, genres} from "../../data/movies";

import {MovieData} from "../../interfaces";
import Movies from "../../components/movies";

const Home = () => {

    const [movieSelected, setMovieSelected] = useState<MovieData>();
    const [similarMovies, setSimilarMovies] = useState<MovieData[] | undefined>();
    const [openModal, setOpenModal] = useState<boolean>(false);


    useEffect(() => {
        if (movieSelected !== undefined) {
            setSimilarMovies(movies.filter(movie => movie.id !== movieSelected.id && movie.genres.find(genre => movieSelected.genres.find(g => g.id === genre.id))));
            // setSimilarMovies(movies.filter( movie => movie.id !== movieSelected.id && movie.genres.find( genre => genre.id === movieSelected.genres[0].id)));
        }
    }, [movieSelected])

    const handleClickMovie = (id: number = 0, openModal: boolean = false) => {
        if (id !== 0) {
            const movie: MovieData | undefined = movies.find(item => item.id === id);
            setMovieSelected(movie)
            setOpenModal(openModal)
            lockScrollBody(true)

        }
    }
    const handleCloseModal = (closeModal: boolean) => {
        setOpenModal(closeModal)
        lockScrollBody(false)

    }

    const lockScrollBody = (lock: boolean) => {

        document
            .getElementsByTagName("html")[0]
            .style
            .overflowY = lock ? "hidden" : "visible";


    }


    return (
        <div className="container-default">
            <Releases getMovieID={handleClickMovie}
                      data={movies}/>

            <Movies getMovieID={handleClickMovie}
                    genres={genres}
                    allMovies={movies}/>


            <Modal data={movieSelected}
                   similar={similarMovies}
                   show={openModal}
                   getSimilarMovieID={handleClickMovie}
                   closeModal={handleCloseModal}/>

        </div>
    );
}

export default Home;
