import React, {useState} from "react";
import MovieCard from "../movie-card";
import {MovieData , MovieGenres} from "../../interfaces";
import {MdKeyboardArrowLeft, MdKeyboardArrowRight} from "react-icons/md";

import {
    Container,
    GenresFilter,
    MovieCardContainer
} from "./styles";



interface MoviesProps {
    genres: MovieGenres[],
    allMovies: MovieData[],
    getMovieID(id: number, show: boolean): void
}

const Movies = (props: MoviesProps) => {

    const [filterId, setFilterId] = useState<number>(0);
    const [gridNumber, setGridNumber] = useState<number>(0);

    const handleGenreClick = (id: number) => {
        setFilterId(id)
    }
    const handleMovieCardClick = (id: number, show: boolean) => {
        props.getMovieID(id, show);
    }

    window.addEventListener('resize', () => getWidthWindow());
    window.addEventListener('load', () => getWidthWindow());

    const getWidthWindow = () => {
        const moviesCardContainerWidth = 1250;
        const widthWindow = window.innerWidth > 1250 ?
            moviesCardContainerWidth / 160 :
            (window.innerWidth - ((window.innerWidth / 100) * 10)) / 160;
        setGridNumber(Math.floor(widthWindow))
    }

    const handleClickScrollGenres = (direction: string) => {
        const listGenres = document.querySelector('.list-filters ul');

        if (listGenres) {
            if (direction === "left"){
                listGenres.scrollBy(-200, 0)
            } else {
                listGenres.scrollBy(200, 0)

            }
        }
    }


    return (
        <Container>

            <GenresFilter>
                <h2>por gênero</h2>

                <div className="list-filters">
                    <button onClick={() => handleClickScrollGenres('left')}> <MdKeyboardArrowLeft/> </button>
                    <ul>
                        <li className={filterId === 0 ? 'active' : ''}
                            onClick={() => handleGenreClick(0)}>todos
                        </li>
                        {
                            props.genres.map(genre => (
                                <li className={filterId === genre.id ? 'active' : ''}
                                    onClick={() => handleGenreClick(genre.id)}
                                    key={genre.id}>{genre.type}</li>
                            ))
                        }
                    </ul>
                    <button onClick={() => handleClickScrollGenres('right')}> <MdKeyboardArrowRight/> </button>
                </div>

            </GenresFilter>

            {
                gridNumber !== 0 && (
                    <MovieCardContainer grid={gridNumber}>
                        {
                            props.allMovies.map(movie => (
                                <MovieCard key={movie.id}
                                           show={(!!movie.genres.find(value => value.id === filterId)) || filterId === 0}
                                           id={movie.id}
                                           poster={movie.poster}
                                           title={movie.title}
                                           handleClick={handleMovieCardClick}/>
                            ))
                        }

                    </MovieCardContainer>
                )
            }




        </Container>
    );
}

export default Movies;
