import React, {useState} from "react";
import {ReleasesContainer, MoviesContainer} from "./styles";

import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

import {MovieData} from "../../interfaces";

import MovieCard from "../movie-card";

interface ReleasesProps {
    data: MovieData[],
    getMovieID(id: number, openModal: boolean): any
}

const Releases = (props: ReleasesProps) => {

    const [movies, setMovies] = useState<MovieData[]>(props.data);

    const responsiveContainer = {
        desktop: {
            breakpoint: {
                max: 3000,
                min: 1170
            },
            items: 7,
        },
        tabletHorizontal: {
            breakpoint: {
                max: 1169,
                min: 1024
            },
            items: 6.2,
        },
        tablet: {
            breakpoint: {
                max: 1023,
                min: 768
            },
            items: 4.5,
        },
        mobileHorizontal: {
            breakpoint: {
                max: 767,
                min: 480
            },
            items: 3.8,
        },
        mobile: {
            breakpoint: {
                max: 479,
                min: 360
            },
            items: 2.2,
        },
        mobileSmall: {
            breakpoint: {
                max: 359,
                min: 320
            },
            items: 2,
        }
    };

    const handleGetId = (id: number, show: boolean) => {
        props.getMovieID(id, show);
    }

    return (
        <ReleasesContainer>
            <h3>ultimos lançamentos</h3>

            <MoviesContainer>

                <Carousel
                    additionalTransfrom={0}
                    arrows={true}
                    autoPlay={true}
                    autoPlaySpeed={3000}
                    centerMode={false}
                    className=""
                    containerClass="container"
                    dotListClass=""
                    draggable
                    focusOnSelect={false}
                    infinite={true}
                    itemClass=""
                    keyBoardControl
                    minimumTouchDrag={80}
                    renderButtonGroupOutside={false}
                    renderDotsOutside={false}
                    responsive={responsiveContainer}
                    showDots={false}
                    sliderClass=""
                    slidesToSlide={1}
                    swipeable>
                    {
                        (movies.slice(0,10).map(movie => (
                            <MovieCard key={movie.id}
                                       show={true}
                                       poster={movie.poster}
                                       title={movie.title}
                                       id={movie.id}
                                       handleClick={handleGetId}/>

                        )))
                    }


                </Carousel>

            </MoviesContainer>
        </ReleasesContainer>
    );
}

export default Releases;


