import styled from "styled-components";
import {colors} from "../../colors";

interface MoviesStyleProps {
    grid: number,
}

export const Container = styled.section`
  max-width: 1250px;
  width: 100%;
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #FFF;

  @media (max-width: 1250px) {
    padding: 60px 5%;
  }

`;

export const GenresFilter = styled.div`
  display: flex;
  flex-direction: column;

  h2 {
    text-transform: uppercase;
    font-size: 24px;
    color: ${colors.primary};
  }

  .list-filters {
    padding: 20px 0;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    
    button {
      min-width: 26px;
      height: 26px;
      border-radius: 13px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      background-color: ${colors.primary};
      color: #FFFFFF;
      font-size: 18px;
      transition: 0.333s ease-in-out;
      
      &:hover {
        background-color: #FFFFFF;
        color: ${colors.primary};
        box-shadow: 0 0 0 1px ${colors.primary};
        
      }
    }
    
   

    ul {
      display: flex;
      height: 40px;
      flex-wrap: nowrap;
      align-items: center;
      grid-column-gap: 10px;
      overflow-x: auto;
      scroll-behavior: smooth;
      scrollbar-base-color: transparent;
      padding: 10px 0;
      margin: 0 10px;
      border-radius: 20px;

      li {
        height: 26px;
        padding: 0 15px;
        border-radius: 13px;
        background-color: rgba(0, 0, 0, 0.1);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 13px;
        font-weight: bold;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        white-space: nowrap;
        cursor: pointer;

        &:hover {
          background-color: ${colors.primary};
          color: #FFFFFF;
        }
      }

      li.active {
        background-color: ${colors.primary};
        color: #FFFFFF;
      }
    }

    /* Hide scrollbar for Chrome, Safari and Opera */
    ul::-webkit-scrollbar {
      display: none;
    }
    
    /* Hide scrollbar for IE, Edge and Firefox */
    ul {
      -ms-overflow-style: none; /* IE and Edge */
    }
  }
`;

export const MovieCardContainer = styled.div<MoviesStyleProps>`
  width: 100%;
  max-width: 1250px;
  display: grid;
  grid-template-columns: repeat(${props => props.grid}, 1fr);
  grid-gap: 20px;
  justify-content: center;
  align-items: center;

  div:first-child {
    margin-left: auto;
  }

  div {
    width: 140px !important;
    margin: 10px auto;
  }

`;

