import React, {useState} from "react";
import {Link} from "react-router-dom";
import {Header, ButtonSearch, ButtonMenu, MenuMobileContainer, MenuMobile} from './styles';
import logo from "../../assets/logo.svg";
import logoColor from "../../assets/logo-color.svg";


const Sidebar = () => {

    const [menuMobileOpen, setMenuMobileOpen] = useState<boolean>(false);

    return (
        <Header>
            <div className="logo">
                <img src={logo} alt=""/>
            </div>


            <ButtonSearch/>

            <ButtonMenu icon={menuMobileOpen} onClick={() => setMenuMobileOpen(!menuMobileOpen)}/>


            <MenuMobileContainer className={menuMobileOpen ? "opened" : ""}>
                <MenuMobile>
                    <div className="button-close" onClick={() => setMenuMobileOpen(false)}/>
                    <div className="head">
                        <img src={logoColor} alt=""/>
                    </div>
                    <h4>menu</h4>
                    <ul>
                        <li><Link to="#">lançamentos</Link></li>
                        <li><Link to="#">filmes</Link></li>
                        <li><Link to="#">séries</Link></li>
                    </ul>
                    <hr/>
                    <ul>
                        <li><Link to="#">contato</Link></li>
                    </ul>
                </MenuMobile>
                <div className="close-menu" onClick={() => setMenuMobileOpen(!menuMobileOpen)}/>
            </MenuMobileContainer>


        </Header>
    );
}

export default Sidebar;
